import React from 'react';
import {Details} from '../components/Details';
import {questionOperations} from '../models/QuestionOperation';
import {List} from '../components/List';
export class Crud extends React.Component{
    constructor(props){
        super(props);
       // this.currentId = 0;
       this.id = 0;
       this.name = '';
        this.state  = {questionObject:{},questions:[],rowColor:''};
    }
    giveMeSelectedQuestionId(currentId){
        console.log("Current Id is ",currentId);
        //this.currentId= currentId;
        var isMarked = questionOperations.markRecord(currentId);
        console.log("After Marked ",questionOperations.questions);
        //var color = isMarked?'table-danger':'';
       // this.setState({...this.state,rowColor:color});
    }
    takeInputName(event){
        this.name = event.target.value;
    }
    takeInputId(event){
        this.id = event.target.value;
       /* console.log("Key is ",key," Event is ",event);
        var obj = this.state.questionObject;
        obj[key] = event.target.value;
        this.setState({questionObject:obj,questions:[]});
        console.log("After Take Input object is  ",this.state.questionObject);
        */

    }
    add(){
        questionOperations.add(this.id,this.name);
        this.setState({questions:questionOperations.questions});
        //this.setState({...this.state,questionObject:{},questions:questionOperations.questions});
        console.log("Add ",questionOperations.questions);
    }
    deleteRec(){
        console.log("Array is ",questionOperations.questions);
        var questionsArray = questionOperations.deleteRecords();
        console.log("After delete record are  ",questionsArray);
        this.setState({questions:questionsArray});
    }
    render(){
        return(
            <div>
                <h4>Welcome , {this.props.userName}</h4>
                <Details takeInputName={this.takeInputName.bind(this)} takeInputId = {this.takeInputId.bind(this)} deleteRec={this.deleteRec.bind(this)} add={this.add.bind(this)}/>
                <List  giveMeSelectedQuestionId={this.giveMeSelectedQuestionId.bind(this)} list={this.state.questions}/>
                
            </div>
        )
}
}