import { Question } from "./Question";

export const questionOperations = {
    questions:[],
    add(id , name){
         var question = new Question(id,name);   
        this.questions.push(question);
        console.log("Record added in array ",this.questions);
        },
        deleteRecords(){
            this.questions = this.questions.filter(questionObject=>questionObject.isMarked==false);
            return this.questions;
        },
        markRecord(id){
            return this.searchById(id).toggle();
        },
        searchById(id){
            return this.questions.find(questionObject=>questionObject.id==id);
        }
}