import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import '../node_modules/bootstrap/dist/css/bootstrap.min.css';
import {Login} from './components/Login';
import {Crud} from './containers/Crud';
class App extends Component {
  constructor(props){
    super(props);
    this.state = {isLogin:false,userName:''};
  }
  isAuth(loginStatus,uid){
    this.setState({isLogin:loginStatus,userName:uid});
  }
  render() {
    return (
     <div className='container'>
       <h1 className='alert-success'>CRUD App Demo</h1>
       {this.state.isLogin?<Crud userName = {this.state.userName}/>:<Login updateLoginStatus={this.isAuth.bind(this)}/>}
       
     </div>
    );
  }
}

export default App;
